.testimonial {
    background-color: $verde;
    font-size: 2.4rem;
    padding: 2rem;
    color: $blanco;
    border-radius: 2rem;

    blockquote {
        padding: 2rem;
        position: relative;

        &::before {
            content: '';
            background-image: url("/build/img/comilla.svg");
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: -4rem;

        }

    }

    p {
        text-align: right;
        color: $blanco;
    }
}